@import "https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap";
body {
  background-color: #555353;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: -1.59vh 0 0;
  font-family: Libre Baskerville, serif;
  display: flex;
}

h1 {
  text-align: center;
}

.fondoCarta {
  background-image: url("fondoCarta.a966a265.png");
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 94%;
  display: flex;
}

header {
  background-color: #f0f8ff;
  width: 33%;
  margin: 0 auto;
}

.container {
  text-align: left;
  background-color: #f0f8ff;
  width: 95%;
  height: 97%;
}

.container > h1 {
  justify-content: center;
  align-items: center;
  height: 10%;
  display: flex;
}

.marco {
  border: 2px solid #12677e;
  width: 80%;
  height: 80%;
  margin: 0 auto;
}

.titulo {
  justify-content: center;
  align-items: center;
  width: 67.5%;
  height: 5vh;
  margin-top: -2.5vh;
  display: flex;
  position: absolute;
}

.index {
  justify-content: center;
  display: flex;
}

.index a {
  color: #12677e;
}

.contenidoCarta {
  color: #12677e;
  background-color: #f0f8ff;
  border: 2px solid #12677e;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 100%;
  display: flex;
}

.logo img {
  width: 12vw;
  height: 25%;
  transform: rotate(7.5deg);
}

.logo {
  margin-top: -4%;
  margin-left: 50%;
  position: absolute;
}

.texto {
  width: 100%;
  margin-left: 2%;
  font-size: 10px;
  display: flex;
}

.platos {
  width: 100%;
  height: 3%;
  display: flex;
}

.platos > p {
  margin: 1% 0% 0% 2%;
  font-size: 15px;
}

.infoPostres {
  margin-left: 2%;
  font-size: 12.5px;
}

.platos > p:first-child {
  border-top: 1px dotted #000;
  width: 85%;
  height: 8%;
}

.mtPlatos {
  margin-top: 5%;
}

.iva {
  justify-content: center;
  font-size: 75%;
  font-weight: bold;
  display: flex;
}

.ivaPrincipal {
  justify-content: center;
  margin-top: 2%;
  font-size: 75%;
  font-weight: bold;
  display: flex;
}

@media screen and (width <= 424px) {
  body {
    background-image: url("fondoCarta.a966a265.png");
    margin: 0;
    padding: 0;
  }

  .fondoCarta {
    text-align: center;
    width: 100vw;
    height: 97vh;
    margin: 0;
  }

  .texto {
    font-size: 15px;
  }

  .container {
    width: 95vw;
    height: 97.5vh;
    margin-top: 1vh;
  }

  .container > h1 {
    font-size: 15px;
  }

  .marco {
    width: 90vw;
    height: 75vh;
    overflow: hidden auto;
  }

  .logo img {
    width: 25vw;
    height: 10vh;
    display: none;
  }

  .logo {
    margin-top: 4vh;
    margin-left: 17.5vw;
    position: absolute;
    transform: rotate(7.5deg);
  }

  .contenidoCarta {
    justify-content: center;
    width: 40vw;
    height: 5vh;
    margin-top: -2.5vh;
    margin-left: 25vw;
    font-size: 10px;
    display: flex;
  }
}

@media screen and (width >= 425px) and (width <= 768px) {
  body {
    overflow: hidden;
  }

  .titulo {
    margin-left: -1vh;
  }

  .fondoCarta {
    text-align: center;
    width: 100%;
    height: 110%;
    margin: 0;
  }

  .container {
    width: 85vw;
    height: 90vh;
    margin-top: 10%;
  }

  .marco {
    width: 75vw;
    height: 70vh;
  }

  .logo {
    display: none;
  }

  .platos > p {
    font-size: 10px;
  }

  .contenidoCarta {
    justify-content: center;
    width: 40vw;
    height: 5vh;
    margin-top: -2.5vh;
    margin-left: 25vw;
    display: flex;
  }

  .iva {
    justify-content: center;
    margin-top: 4%;
    font-weight: bold;
  }
}

